<template>
  <div class="my">
    <div class="myTop">
      <ul class="myTopImg">
        <img
          @click="gotoHis"
          src="@/assets/phone/AppVisitorHome/back.png"
          alt
        />
        <li @click="resetTokenBtn">退出</li>
      </ul>
      <div class="myTopUser">
        <div class="userName">{{ getUserInfoData.userName }}</div>

        <div
          class="userImg"
          @click="dialogFlag = true"
          :style="{
            background:
              'url(' +
              setHeadPortrait(getUserInfoData.headPortraitBase64) +
              ') no-repeat',
            backgroundSize: 'cover',
          }"
        ></div>
      </div>
    </div>
    <div class="myContent">
      <ul>
        <div class="ContentTitle">个人信息</div>
        <li>
          <div>用户名</div>
          <div>
            {{ getUserInfoData.userName }}
            <img src="@/assets/phone/right1.png" alt />
          </div>
        </li>
        <li>
          <div>手机号</div>
          <div>
            {{ getUserInfoData.phoneNumber }}
            <img src="@/assets/phone/right1.png" alt />
          </div>
        </li>
        <li
          @click="$router.push({ name: 'modify', params: { getUserInfoData } })"
        >
          <div>修改密码</div>
          <div>
            ********
            <img src="@/assets/phone/right1.png" alt />
          </div>
        </li>
        <li @click="resetInfo('idCard', getUserInfoData.idNumber)">
          <div>身份证号</div>
          <div>
            {{ getUserInfoData.idNumber || "未填写" }}
            <img src="@/assets/phone/right1.png" alt />
          </div>
        </li>
        <li @click="resetInfo('address', getUserInfoData.homeAddress)">
          <div>家庭住址</div>
          <div>
            {{ getUserInfoData.homeAddress || "未填写" }}
            <img src="@/assets/phone/right1.png" alt />
          </div>
        </li>
      </ul>
      <ul>
        <div class="ContentTitle">我的登记</div>
        <li @click="routerTo('/activitylist')">
          <div>活动报名</div>
          <div>
            <img src="@/assets/phone/right1.png" alt />
          </div>
        </li>
        <li @click="routerTo('/VisitorAppointment')">
          <div>访客预约</div>
          <div>
            <img src="@/assets/phone/right1.png" alt />
          </div>
        </li>
        <li @click="routerTo('/VolunteerRecruitment')">
          <div>志愿者报名</div>
          <div>
            <!-- {{ getUserInfoData.fullName }} -->
            <img src="@/assets/phone/right1.png" alt />
          </div>
        </li>
      </ul>
    </div>
    <div class="myBom">
      <ul>
        <li>
          <img src="@/assets/phone/my/zhuCe.png" alt />
          <div>软件注册</div>
        </li>
        <li>
          <img src="@/assets/phone/my/system_information.png" alt />
          <div>系统信息</div>
        </li>
        <li>
          <img src="@/assets/phone/my/About_us.png" alt />
          <div>关于我们</div>
        </li>
      </ul>
    </div>
    <!-- :actions="actions" -->
    <van-action-sheet
      v-model:show="dialogFlag"
      cancel-text="取消"
      close-on-click-action
      @cancel="dialogFlag = false"
    >
      <van-uploader
        :show="uploaderShow"
        :after-read="afterRead"
        :max-size="1024 * 1024 * 5"
        @oversize="onOversize"
        accept="image/*"
        class="uploader"
      >
        <div
          class="actionSheetMySelf"
          v-for="(item, index) in actions"
          :key="index"
          @click="onSelect(item)"
        >
          {{ item.name }}
        </div>
      </van-uploader>
    </van-action-sheet>
  </div>
</template>

<script>
import store from "@/store";
import { getUserInfo } from "@/api/case/board";
import { updateUser, uploadAvatar } from "@/api/data/reservation";
import { ActionSheet, Uploader } from "vant";

export default {
  components: { "van-action-sheet": ActionSheet, "van-uploader": Uploader },
  data() {
    return {
      dialogFlag: false,
      getUserInfoData: {},
      actions: [
        // { name: "拍照", value: "camera" },
        { name: "选择照片", value: "" },
      ],
      uploadUrl: process.env.VUE_APP_BASE_API + "/file/upload",
      isCamera: "",
      uploaderShow: false,
      imgTypeExts: [
        "image/jpg",
        "image/jpeg",
        "image/png",
        "image/gif",
        "image/bpm",
      ],
      name: "",
    };
  },

  methods: {
    gotoHis() {
      this.$router.push({ path: this.name });
    },
    afterRead(file, e) {
      if (this.imgTypeExts.find((c) => c == file.file.type)) {
        let _this = this;
        let size = file.file.size;
        //压缩到200kb
        new this.$compressorjs(file.file, {
          quality: (200 / size) * 1024,
          success(result) {
            let formData = new FormData();
            formData.append("file", result);
            uploadAvatar(formData).then((res) => {
              _this.dialogFlag = false;
              _this.$message.success("修改成功");
              _this.getUserInfo();
            });
          },
          error(err) {
            _this.$message.warning("上传失败，请重新选择!");
          },
        });
      } else {
        this.$message.warning("支持" + this.imgTypeExts.join("、") + "格式");
      }
    },
    onOversize() {
      this.$message.warning("图片大小不能超过5MB");
    },
    setHeadPortrait(val) {
      if (val) {
        return "data:image/png;base64," + val;
      } else {
        return require("@/assets/phone/AppVisitorHome/bg1.png");
      }
    },
    onSelect(val) {
      let _this = this;
      this.isCamera = val.value;
      this.uploaderShow = true;
    },
    resetInfo(val, value) {
      let title = "提示";
      if (val == "idCard") {
        title = "身份证号";
      } else {
        title = "家庭住址";
      }
      let _this = this;
      this.$messagebox({
        $type: "prompt",
        title: title,
        showInput: true,
        message: " ",
        inputValue: value,
        inputType: "text",
        inputValidator: (val) => {
          if (val === null || val === "") {
            return false;
          } else {
            return true;
          }
        },
        inputErrorMessage: "请输入" + title,
        showCancelButton: true,
        confirmButtonText: "提交",
      })
        .then(({ value, action }) => {
          if (value != "" && value != null) {
            let obj = {};
            if (val == "idCard") {
              if (_this.blurID(value)) {
                obj.idNumber = value;
                if (parseInt(value.substr(16, 1)) % 2 == 1) {
                  obj.sex = "男";
                } else {
                  obj.sex = "女";
                }
              } else {
                return;
              }
            } else {
              obj.homeAddress = value;
            }

            updateUser(obj).then((res) => {
              _this.getUserInfo();
            });
          }
        })
        .catch(() => {});
    },
    blurID(val) {
      let sfz =
        /^\d{6}((((((19|20)\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|(((19|20)\d{2})(0[13578]|1[02])31)|((19|20)\d{2})02(0[1-9]|1\d|2[0-8])|((((19|20)([13579][26]|[2468][048]|0[48]))|(2000))0229))\d{3})|((((\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|((\d{2})(0[13578]|1[02])31)|((\d{2})02(0[1-9]|1\d|2[0-8]))|(([13579][26]|[2468][048]|0[048])0229))\d{2}))(\d|X|x)$/;
      if (!sfz.test(val)) {
        this.$message.warning("身份证不正确");
        return false;
      } else {
        return true;
      }
    },
    routerTo(str) {
      this.$router.push(str);
    },
    resetTokenBtn() {
      store.state.user.token = "";
      window.sessionStorage.setItem("SET_TOKEN", "");
      this.$router.push("/SignIn");
    },
    getUserInfo() {
      let obj = {
        uuid:
          window.sessionStorage.getItem("SET_TOKEN") || store.state.user.token,
      };
      getUserInfo(obj).then((res) => {
        this.getUserInfoData = res.data;
      });
    },
  },
  mounted() {
    this.getUserInfo();
  },
  beforeRouteEnter(to, from, next) {
    console.log(from);
    next((vm) => {
      if (from.path == "/modify") {
        vm.name = "/reserve";
      } else {
        vm.name = from.path;
      }
    });
  },
};
</script>
<style lang='scss' scoped>
.my {
  width: 100vw;
  font-size: 3vw;
  .myTop {
    width: 100%;
    height: 30vh;
    background-image: url("../../../../assets/phone/my/bk01.png");
    background-repeat: no-repeat;
    background-size: 100%;
    margin-top: -4vw;
    .myTopImg {
      width: 87%;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      list-style: none;
      img {
        width: 7vw;
        height: 7vw;
        margin-top: 8vw;
      }
      li {
        width: 18vw;
        color: rgba(255, 255, 255, 0.671);
        font-size: 3.6vw;
        text-align: center;
        height: 4vh;
        line-height: 4vh;
        border-radius: 6vw;
        background-color: rgba(184, 181, 181, 0.219);
        margin-top: 8vw;
      }
    }
    .myTopUser {
      width: 20vw;
      margin: 6vw auto 0;
      .userName {
        text-align: center;
        color: white;
        font-size: 5vw;
      }
      .userImg {
        width: 20vw;
        height: 20vw;
        margin-top: 3vw;
        border-radius: 100%;
        border: 0.5vw solid green;
        background-size: cover !important;
      }
    }
  }
  .myContent {
    width: 100vw;
    ul {
      width: 95vw;
      margin: 4vw auto;
      font-size: 4vw;
      box-sizing: border-box;
      list-style: none;
      border-radius: 2vw;
      font-weight: bold;
      padding: 2vw;
      background-color: #fbfbfb;
      border: 0.5vw solid rgba(195, 195, 195, 0.623);
      .ContentTitle {
        font-size: 4vw;
        margin: 1.5vw 0 3.5vw 0;
        color: #8d8c8cb0;
      }
      li {
        padding: 0 1vw;
        display: flex;
        justify-content: space-between;
        margin-top: 2vw;
        padding-bottom: 1vw;
        border-bottom: 0.4vw solid rgba(172, 169, 169, 0.774);
        div:nth-child(2) {
          color: #b6b6b6;
          img {
            width: 4vw;
          }
        }
      }
    }
  }
  .myBom {
    width: 100vw;
    position: fixed;
    bottom: 0;
    ul {
      width: 95%;
      height: 21vw;
      margin: 0 auto;
      background-image: url("../../../../assets/phone/my/bk02.png");
      background-size: 100%;
      background-repeat: no-repeat;
      display: flex;
      justify-content: center;
      list-style: none;
      li {
        width: 20vw;
        img {
          width: 7vw;
          margin: 3vw 0 0 6vw;
        }
        div {
          text-align: center;
        }
      }
    }
  }
}
.actionSheetMySelf {
  line-height: 22px;
  width: calc(100% - 20px);
  text-align: center;
  display: block;
  padding: 14px 10px;
  font-size: 16px;
  cursor: pointer;
}
</style>
<style lang='scss'>
.van-uploader {
  width: 100%;
}
.van-uploader__input-wrapper {
  width: 100%;
}
</style>